import React from 'react';
import './App.css';
import {Panel, PanelHeader, PanelHeaderBack, View} from "@vkontakte/vkui";
import '@vkontakte/vkui/dist/vkui.css';
import fire from './functions/firebase';
import checkUserExists from './functions/checkUserExist';
import bridge from '@vkontakte/vk-bridge';

class App extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            activePanel: 'main',
            userData: {}
        };
    }
    componentDidMount() {
        bridge.send("VKWebAppInit", {})
            .then(
                r => bridge.send("VKWebAppGetUserInfo", {})
                    .then(
                        z => {this.setState({
                                userData: z
                            });
                            checkUserExists(z.id);
                        }
                    )
            );
    }
    componentWillUnmount() {

    }
    render() {
        return (
            <View activePanel={this.state.activePanel}>
                <Panel id="main">
                    <PanelHeader>RPG</PanelHeader>
                </Panel>
                <Panel id="hello">

                </Panel>
            </View>
        );
    }
}

export default App;
